html:not([data-scale]) .am-list-item:not(:last-child) .am-list-line::after {
  height: 0px!important;
}
.transfer-token-page .title {
  margin-top: 0.9375rem;
  margin-left: 0.9375rem;
  font-size: 1.125rem;
  font-weight: 400;
  color: #ffffff;
}
.transfer-token-page .transfer-input:nth-child(1) {
  padding-top: 1.5625rem;
}
.transfer-token-page .transfer-input {
  position: relative;
  padding-right: 1.5625rem;
  padding-left: 1.5625rem;
  padding-top: 0.9375rem;
  padding-bottom: 0.625rem;
}
.transfer-token-page .transfer-input .token .am-input-extra {
  color: #fff;
}
.transfer-token-page .transfer-input .other .am-input-extra {
  color: #00D2FF;
}
.transfer-token-page .transfer-input .ant-input-suffix {
  color: #fff;
}
.transfer-token-page .transfer-input .am-list-item {
  background-color: #13141C;
  border-bottom: 1px solid #ccc;
}
.transfer-token-page .transfer-input .ant-form-explain {
  color: red;
}
.transfer-token-page .transfer-input input {
  box-sizing: border-box;
  width: 100%;
  height: 2.8125rem;
  border: none;
  color: #666;
  padding-right: 20px;
  background-color: #13141C;
  border-bottom: 1px solid #ccc;
}
.transfer-token-page .transfer-input i {
  position: absolute;
  right: 1.5625rem;
  top: 45%;
  margin-bottom: -1.40625rem;
  color: #fff;
}
.transfer-token-page .choose-username,
.transfer-token-page .password {
  margin-top: 0.9375rem;
  margin-left: 0.9375rem;
  font-size: 0.875rem;
  font-weight: 400;
  color: #ffffff;
}
.transfer-token-page .message-error {
  display: flex;
  justify-content: flex-end;
  height: 0.625rem;
  padding-right: 0.9375rem;
  padding-top: 0.625rem;
  font-size: 0.5625rem;
  color: #FF3333;
}
.transfer-token-page .customButton {
  padding-top: 3.125rem;
  padding-right: 0.9375rem;
  padding-left: 0.9375rem;
}
